<template>
    <div class="container">
        <v-skeleton-loader
            v-if="showLoader"
            type="button, table-tbody"
            max-width="300"
            max-height="400"
        />
        <div v-else>
            <v-row class="container__row">
                <v-btn class="container__row__button"
                @click="goBackToUserList"
                >
                <v-icon 
                    left
                    >
                    mdi-chevron-left
                </v-icon>
                    go back
                </v-btn>
            </v-row>
            <table class="table">
                <tbody>
                    <tr class="table__row">
                        <td class="table__row__data table__row__title">Name</td>
                        <td class="table__row__data">{{ getUserData?.name }}</td>
                    </tr>
                    <tr class="table__row">
                        <td class="table__row__data table__row__title">User Name</td>
                        <td class="table__row__data">{{ getUserData?.username }}</td>
                    </tr>
                    <tr class="table__row">
                        <td class="table__row__data table__row__title">Email</td>
                        <td class="table__row__data">{{ getUserData?.email }}</td>
                    </tr>
                    <tr class="table__row">
                        <td class="table__row__data table__row__title">phone</td>
                        <td class="table__row__data">{{ getUserData?.phone }}</td>
                    </tr>
                    <tr class="table__row">
                        <td class="table__row__data table__row__title">website</td>
                        <td class="table__row__data">{{ getUserData?.website }}</td>
                    </tr>
                    <tr class="table__row">
                        <td class="table__row__data table__row__title">Address</td>
                        <td class="table__row__data">{{ getUserData?.address.suite }}, <br> {{ getUserData?.address.street
                            }}, <br> {{ getUserData?.address.city }}, <br> {{ getUserData?.address.zipcode }}</td>
                    </tr>
                    <tr class="table__row">
                        <td class="table__row__data table__row__title">Company</td>
                        <td class="table__row__data">{{ getUserData?.company.name }}, <br> {{ getUserData?.company.bs }},
                        <br> {{ getUserData?.company.catchPhrase }}</td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script setup lang="ts">
import axios from 'axios'
import { computed, defineProps, onMounted, ref } from 'vue'

import { User } from '@/types/Type'
import router from '@/router/index'

const props = defineProps(['id'])
const userData = ref<User>()
const showLoader = ref<boolean>(true)

const getUserData = computed(() => userData.value)

const goBackToUserList = () => {
    router.push('/userList')
}

onMounted(async () => {
    const response = await axios ({
        method: 'get',
        url: `https://jsonplaceholder.typicode.com/users/${props.id}`
    })
    userData.value = response.data
    showLoader.value = false
})
</script>

